/* ---- OVERLAY ---- */
.payment-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.55);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 3000;
  backdrop-filter: blur(3px);
}

/* ---- PANEL ---- */
.payment-panel {
  width: 420px;
  max-height: 85vh;
  background: #ffffff;
  border-radius: 14px;
  overflow-y: auto;
  box-shadow: 0 6px 30px rgba(0, 0, 0, 0.25);
  padding: 24px 28px;
  animation: paymentFadeIn 0.35s ease-out;
  font-family: "Inter", "Helvetica", sans-serif;
}

@keyframes paymentFadeIn {
  from { opacity: 0; transform: translateY(25px); }
  to { opacity: 1; transform: translateY(0); }
}

/* ---- TITLE ---- */
.payment-title {
  font-size: 22px;
  font-weight: 650;
  color: #111;
  margin-bottom: 18px;
  border-bottom: 2px solid #f2f2f2;
  padding-bottom: 10px;
}

/* ---- EMPTY CART ---- */
.payment-empty {
  text-align: center;
  color: #666;
  font-size: 15px;
  padding: 20px 0;
}

/* ---- CART ITEMS ---- */
.payment-cart-items {
  margin-bottom: 18px;
  background: #fafafa;
  border-radius: 10px;
  padding: 12px 15px;
  border: 1px solid #e5e5e5;
}

.payment-cart-row {
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  font-size: 14.5px;
  color: #222;
  border-bottom: 1px solid #eaeaea;
}

.payment-cart-row:last-child {
  border-bottom: none;
}

/* ---- TOTAL ---- */
.payment-total {
  text-align: right;
  font-size: 20px;
  font-weight: 700;
  color: #0057a0; /* eBay Blue */
  margin-top: 10px;
  margin-bottom: 20px;
  background: #fef7d3;  /* light yellow highlight */
  padding: 12px 14px;
  border-radius: 10px;
  border: 1px solid #ffe48e;
}

/* ---- BUTTONS ---- */
.payment-actions {
  display: flex;
  justify-content: space-between;
  gap: 12px;
}

/* Cancel Button */
.payment-close-btn {
  width: 50%;
  padding: 12px 18px;
  background: #e5e5e5;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  font-size: 15px;
  color: #444;
  font-weight: 550;
  transition: all 0.2s ease;
}

.payment-close-btn:hover {
  background: #d6d6d6;
}

/* Pay (Invoice) Button */
.payment-pay-btn {
  width: 50%;
  padding: 12px 18px;
  background: #0064d2;   /* eBay rich blue */
  border-radius: 10px;
  border: 1px solid #0052ac;
  cursor: pointer;
  font-size: 15px;
  color: white;
  font-weight: 600;
  transition: all 0.25s ease;
}

.payment-pay-btn:hover {
  background: #0158bb;
  box-shadow: 0 4px 12px rgba(1, 92, 170, 0.3);
}

.payment-pay-btn:disabled {
  background: #93c0f7;
  cursor: not-allowed;
  border: 1px solid #7bb0f3;
  box-shadow: none;
}

/* ---- SCROLLBAR (eBay clean style) ---- */
.payment-panel::-webkit-scrollbar {
  width: 6px;
}

.payment-panel::-webkit-scrollbar-thumb {
  background: #c3c3c3;
  border-radius: 10px;
}

.payment-panel::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

