/* ------------------------------------
   GLOBAL CONTAINER
------------------------------------ */
.chatpage-container {
  display: grid;
  grid-template-columns: 340px 1fr;
  width: 100%;
  height: 100vh;
  background: #fff;
  border-top: 1px solid #e5e5e5;
  font-family: "Inter", sans-serif;
  overflow: hidden; /* prevent whole-page scroll */
}

/* ------------------------------------
   LEFT SIDEBAR (Conversations List)
------------------------------------ */
.chatpage-sidebar {
  display: flex;
  flex-direction: column;
  border-right: 1px solid #e0e0e0;
  background: #fafafa;
  height: 100%;
  overflow: hidden;
}

.chatpage-title {
  font-size: 20px;
  font-weight: 700;
  padding: 16px;
  border-bottom: 1px solid #ddd;
  flex-shrink: 0;
}

.chatpage-conversations {
  flex: 1;
  overflow-y: auto; /* independent scroll */
  padding-bottom: 20px;
}

.chatpage-conv-item {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  padding: 12px 16px;
  cursor: pointer;
  border-bottom: 1px solid #f0f0f0;
  transition: background 0.2s;
}

.chatpage-conv-item:hover {
  background: #eef5ff;
}

.chatpage-conv-item.active {
  background: #d8ebff;
}

.chatpage-avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #0073e6;
  color: #fff;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.chatpage-conv-content {
  flex: 1;
  overflow: hidden;
}

.chatpage-conv-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.chatpage-conv-name {
  font-weight: 600;
  font-size: 14px;
  color: #222;
}

.chatpage-unread {
  background: #d93025;
  color: white;
  font-size: 12px;
  padding: 2px 6px;
  border-radius: 10px;
}

.chatpage-conv-text {
  color: #666;
  font-size: 13px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-top: 2px;
}

/* ------------------------------------
   RIGHT CHAT PANEL (Messages Area)
------------------------------------ */
.chatpage-right {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: #fff;
  overflow: hidden;
}

/* HEADER (fixed top in right panel) */
.chatpage-header {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14px 20px;
  border-bottom: 1px solid #e5e5e5;
  background: #fafafa;
  z-index: 10;
}

.chatpage-header-left {
  display: flex;
  align-items: center;
  gap: 12px;
}

.chatpage-avatar-lg {
  width: 48px;
  height: 48px;
  background: #0073e6;
  color: #fff;
  font-weight: 600;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.chatpage-header-left h3 {
  margin: 0;
  font-size: 16px;
}

.chatpage-header-left span {
  font-size: 13px;
  color: #555;
}

/* ------------------------------------
   MESSAGES SCROLL AREA
------------------------------------ */
.chatpage-messages {
  flex: 1;
  overflow-y: auto; /* independent scroll for messages */
  padding: 20px;
  background: #f9f9f9;
  scroll-behavior: smooth;
}

.chatpage-message {
  display: flex;
  margin-bottom: 12px;
}

.chatpage-message.sent {
  justify-content: flex-end;
}

.chatpage-message.received {
  justify-content: flex-start;
}

.chatpage-bubble {
  background: #dcf8c6;
  color: #111;
  border-radius: 18px;
  padding: 10px 14px;
  font-size: 14px;
  max-width: 70%;
  position: relative;
  word-wrap: break-word;
}

.chatpage-message.received .chatpage-bubble {
  background: #fff;
  border: 1px solid #ddd;
}

.chatpage-time {
  display: block;
  font-size: 11px;
  color: #777;
  margin-top: 4px;
  text-align: right;
}

/* ------------------------------------
   INPUT BAR (fixed bottom)
------------------------------------ */
.chatpage-input-bar {
  flex-shrink: 0;
  display: flex;
  padding: 12px 16px;
  border-top: 1px solid #ddd;
  background: #fff;
  align-items: center;
  gap: 10px;
  z-index: 5;
}

.chatpage-input-bar input {
  flex: 1;
  border: none;
  background: #f2f2f2;
  border-radius: 999px;
  padding: 10px 16px;
  font-size: 14px;
  outline: none;
}

.chatpage-send-btn {
  background: #0073e6;
  color: #fff;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  cursor: pointer;
  font-size: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.2s;
}

.chatpage-send-btn:hover {
  background: #005bb5;
}

/* ------------------------------------
   EMPTY / PLACEHOLDER STATES
------------------------------------ */
.chatpage-placeholder {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #777;
  flex: 1;
  text-align: center;
}

.chatpage-placeholder h3 {
  margin-bottom: 6px;
  font-size: 18px;
}

.chatpage-placeholder p {
  font-size: 14px;
}

/* ------------------------------------
   CUSTOM SCROLLBARS
------------------------------------ */
.chatpage-sidebar::-webkit-scrollbar,
.chatpage-messages::-webkit-scrollbar {
  width: 8px;
}

.chatpage-sidebar::-webkit-scrollbar-thumb,
.chatpage-messages::-webkit-scrollbar-thumb {
  background: #ccc;
  border-radius: 10px;
}

.chatpage-sidebar::-webkit-scrollbar-thumb:hover,
.chatpage-messages::-webkit-scrollbar-thumb:hover {
  background: #999;
}

/* ------------------------------------
   RESPONSIVE (for smaller screens)
------------------------------------ */
@media (max-width: 992px) {
  .chatpage-container {
    grid-template-columns: 280px 1fr;
  }
}

@media (max-width: 768px) {
  .chatpage-container {
    grid-template-columns: 1fr;
  }
  .chatpage-sidebar {
    display: none; /* hide sidebar on mobile */
  }
}
