/* -----------------------------
   Banner Container (Hero Area)
------------------------------ */
.banner-container {
  width: 100%;
  height: 60vh; /*  large hero section */
  max-height: 750px;
  position: relative;
  overflow: hidden;
  border-radius: 0;
  margin-bottom: 32px;
}

/* -----------------------------
   Each Slide
------------------------------ */
.banner-slide {
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  top: 0;
  left: 100%; /* start off-screen */
  opacity: 0;
  transition: all 0.8s ease-in-out;
  display: flex;
  align-items: flex-end;     /*  button at bottom */
  justify-content: flex-start; /*  button on left */
  padding: 0 0 50px 50px;    /*  spacing from edges */
}

.banner-slide.active {
  left: 0;
  opacity: 1;
}

/* -----------------------------
   Overlay (for better contrast)
------------------------------ */
.banner-slide::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    rgba(0, 0, 0, 0.25),
    rgba(0, 0, 0, 0.25)
  );
  z-index: 1;
}

/* -----------------------------
   Button Styling
------------------------------ */
.banner-button {
  position: relative;
  z-index: 2; /* above overlay */
  background-color: rgba(0, 0, 0, 0.6);
  color: #fff;
  border: none;
  padding: 12px 24px;
  border-radius: 6px;
  font-weight: 600;
  cursor: pointer;
  font-size: 16px;
  transition: all 0.3s ease;
}

.banner-button:hover {
  background-color: rgba(0, 0, 0, 0.85);
  transform: translateY(-2px);
}

/* -----------------------------
   Responsive Adjustments
------------------------------ */
@media (max-width: 1024px) {
  .banner-container {
    height: 55vh;
  }

  .banner-slide {
    padding: 0 0 40px 30px;
  }

  .banner-button {
    font-size: 14px;
    padding: 10px 20px;
  }
}

@media (max-width: 768px) {
  .banner-container {
    height: 45vh;
  }

  .banner-slide {
    padding: 0 0 30px 20px;
  }

  .banner-button {
    font-size: 13px;
    padding: 8px 16px;
  }
}

@media (max-width: 480px) {
  .banner-container {
    height: 35vh;
  }

  .banner-slide {
    padding: 0 0 24px 16px;
  }

  .banner-button {
    font-size: 12px;
    padding: 6px 14px;
  }
}
