.testimonials {
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
}

.testimonials-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-bottom: 4rem;
}

.testimonial-card {
  background: white;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}

.rating {
  color: var(--warning);
  font-size: 1.25rem;
  margin-bottom: 1rem;
}

.testimonial-card blockquote {
  font-style: italic;
  color: var(--text-dark);
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

.author {
  border-top: 1px solid var(--border);
  padding-top: 1rem;
}

.author strong {
  display: block;
  color: var(--text-dark);
  margin-bottom: 0.25rem;
}

.author span {
  font-size: 0.875rem;
  color: var(--text-light);
}

.case-studies h3 {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 2rem;
  color: var(--text-dark);
}

.case-studies-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.case-study {
  background: white;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.case-study h4 {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: var(--text-dark);
}

.result {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--success);
  margin-bottom: 1rem;
}

.case-study p {
  color: var(--text-light);
  margin-bottom: 1.5rem;
  line-height: 1.6;
}