/* ===========================
   Container & Heading
=========================== */
.addproduct-add-container {
  background: white;
  border-radius: 16px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
  padding: 32px;
  max-width: 600px;
  width: 90%;
  margin: 20px auto;
  border: 1px solid #e1e8ed;
  position: relative;
}

.addproduct-add-heading {
  font-size: 28px;
  font-weight: 700;
  color: #2d3748;
  text-align: center;
  margin-bottom: 32px;
  padding-bottom: 16px;
  border-bottom: 2px solid #f7fafc;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.addproduct-add-container form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* ===========================
   Inputs & Textareas
=========================== */
.addproduct-add-input {
  padding: 14px 16px;
  border: 2px solid #e2e8f0;
  border-radius: 12px;
  font-size: 16px;
  transition: all 0.3s ease;
  background-color: #fafafa;
  color: #2d3748;
  font-family: inherit;
}

.addproduct-add-input:focus {
  outline: none;
  border-color: #667eea;
  background-color: white;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
  transform: translateY(-2px);
}

.addproduct-add-input:hover {
  border-color: #cbd5e0;
  background-color: white;
}

.addproduct-textarea {
  min-height: 120px;
  resize: vertical;
  font-family: inherit;
  line-height: 1.5;
}

.addproduct-add-input::placeholder {
  color: #a0aec0;
}

/* ===========================
   Variants Section
=========================== */
.addproduct-variants-title {
  font-size: 20px;
  font-weight: 700;
  color: #4a5568;
  margin-top: 10px;
  margin-bottom: 8px;
  text-align: left;
}

.addproduct-variant-row {
  display: flex;
  align-items: center;
  gap: 10px;
  background-color: #f8fafc;
  border-radius: 10px;
  border: 2px dashed #e2e8f0;
  padding: 12px;
  transition: all 0.3s ease;
}

.addproduct-variant-row:hover {
  border-color: #667eea;
  background-color: #f0f4ff;
}

.variant-input {
  flex: 1;
  padding: 12px 10px;
  border: 2px solid #e2e8f0;
  border-radius: 8px;
  font-size: 15px;
  transition: all 0.3s ease;
  background-color: #fff;
}

.variant-input:focus {
  border-color: #667eea;
  outline: none;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.addproduct-remove-btn {
  background-color: #fed7d7;
  color: #c53030;
  border: none;
  border-radius: 50%;
  width: 34px;
  height: 34px;
  cursor: pointer;
  font-size: 18px;
  line-height: 1;
  font-weight: bold;
  transition: all 0.3s ease;
}

.addproduct-remove-btn:hover {
  background-color: #feb2b2;
  transform: scale(1.05);
}

.addproduct-addvariant-btn {
  align-self: flex-start;
  background: linear-gradient(135deg, #48bb78 0%, #38a169 100%);
  color: white;
  border: none;
  padding: 10px 18px;
  border-radius: 8px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 8px;
  box-shadow: 0 2px 8px rgba(72, 187, 120, 0.3);
}

.addproduct-addvariant-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 16px rgba(72, 187, 120, 0.4);
}

/* ===========================
   Image Upload
=========================== */
.addproduct-image-row {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 16px;
  background-color: #f8fafc;
  border-radius: 12px;
  border: 2px dashed #e2e8f0;
  transition: all 0.3s ease;
}

.addproduct-image-row:hover {
  border-color: #667eea;
  background-color: #f0f4ff;
}

.addproduct-image-label {
  font-weight: 600;
  color: #4a5568;
  min-width: 80px;
  font-size: 14px;
}

.addproduct-image-row input[type="file"] {
  flex: 1;
  padding: 8px;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  background: white;
  font-size: 14px;
  color: #4a5568;
}

.addproduct-file-name {
  padding: 12px 16px;
  background-color: #edf2f7;
  border-radius: 8px;
  font-size: 14px;
  color: #4a5568;
  border-left: 4px solid #667eea;
  margin: -8px 0 0 0;
  word-break: break-all;
}

/* ===========================
   Buttons
=========================== */
.addproduct-submit-btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 16px 32px;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 8px;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

.addproduct-submit-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
}

.addproduct-submit-btn:disabled {
  background: #a0aec0;
  cursor: not-allowed;
  transform: none;
  opacity: 0.7;
  animation: addproduct-pulse 1.5s ease-in-out infinite;
}

.addproduct-cancel-btn {
  width: 100%;
  background: transparent;
  color: #718096;
  border: 2px solid #e2e8f0;
  padding: 14px 32px;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 16px;
}

.addproduct-cancel-btn:hover {
  background-color: #f7fafc;
  border-color: #cbd5e0;
  color: #4a5568;
  transform: translateY(-2px);
}

/* ===========================
   Animation
=========================== */
@keyframes addproduct-pulse {
  0% { opacity: 1; }
  50% { opacity: 0.7; }
  100% { opacity: 1; }
}

/* ===========================
   Responsive Design
=========================== */
@media (max-width: 768px) {
  .addproduct-add-container {
    padding: 24px;
    margin: 16px auto;
    width: 95%;
  }

  .addproduct-add-heading {
    font-size: 24px;
    margin-bottom: 24px;
  }

  .addproduct-add-container form {
    gap: 16px;
  }

  .addproduct-variant-row {
    flex-direction: column;
    align-items: stretch;
  }

  .variant-input {
    width: 100%;
  }

  .addproduct-addvariant-btn {
    width: 100%;
    text-align: center;
  }

  .addproduct-image-row {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
    padding: 12px;
  }

  .addproduct-submit-btn,
  .addproduct-cancel-btn {
    padding: 14px 24px;
    font-size: 15px;
  }
}

@media (max-width: 480px) {
  .addproduct-add-container {
    padding: 20px;
    border-radius: 12px;
  }

  .addproduct-add-heading {
    font-size: 22px;
    margin-bottom: 20px;
  }

  .addproduct-add-input {
    padding: 10px 12px;
    font-size: 14px;
  }

  .addproduct-textarea {
    min-height: 100px;
  }

  .addproduct-submit-btn,
  .addproduct-cancel-btn {
    padding: 12px 20px;
    font-size: 14px;
  }
}

/* ===========================
   Focus & Error States
=========================== */
.addproduct-add-input:focus-visible,
.addproduct-image-row input[type="file"]:focus-visible,
.addproduct-submit-btn:focus-visible,
.addproduct-cancel-btn:focus-visible {
  outline: 2px solid #667eea;
  outline-offset: 2px;
}

.addproduct-add-input.error {
  border-color: #e53e3e;
  background-color: #fed7d7;
}

.addproduct-error-message {
  color: #e53e3e;
  font-size: 14px;
  margin-top: -8px;
  margin-bottom: 8px;
  padding: 0 8px;
}
