/* ---------------------------
   Page Layout (Full Body)
---------------------------- */
.quotations-page {
  width: 100%;
  min-height: 100vh;
  background: #fafafa;
  display: flex;
  flex-direction: column;
  font-family: "Inter", sans-serif;
}

/* ---------------------------
   Header (Sticky)
---------------------------- */
.quotations-header {
  position: sticky;
  top: 0;
  background: #fff;
  padding: 20px 60px;
  border-bottom: 1px solid #e5e5e5;
  z-index: 10;
}

.quotations-header h1 {
  font-size: 24px;
  font-weight: 700;
  margin: 0;
}

.quotations-header p {
  font-size: 14px;
  color: #666;
  margin-top: 4px;
}

/* ---------------------------
   Filter Buttons
---------------------------- */
.quotations-filters {
  display: flex;
  gap: 10px;
  padding: 16px 60px;
  border-bottom: 1px solid #e5e5e5;
  background: #fff;
  position: sticky;
  top: 80px;
  z-index: 9;
}

.quotations-filter-btn {
  padding: 6px 14px;
  border: 1px solid #ccc;
  background: #fff;
  border-radius: 20px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s;
}

.quotations-filter-btn.active {
  background: #111;
  color: #fff;
  border-color: #111;
}

/* ---------------------------
   List Container
---------------------------- */
.quotations-list-container {
  flex: 1;
  padding: 30px 60px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* ---------------------------
   Item Card
---------------------------- */
.quotations-item-card {
  background: #fff;
  border: 1px solid #e5e5e5;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
  overflow: hidden;
}

/* Status Banner */
.quotations-status-banner {
  padding: 12px 18px;
  font-size: 14px;
  background: #fefbe8;
  color: #555;
  border-bottom: 1px solid #eee;
}

/* Body */
.quotations-item-body {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px;
  gap: 20px;
}

.quotations-item-left {
  display: flex;
  align-items: center;
  gap: 16px;
}

.quotations-item-img {
  width: 80px;
  height: 80px;
  object-fit: contain;
  background: #f8f8f8;
  border-radius: 6px;
  border: 1px solid #eee;
}

.quotations-item-left h3 {
  font-size: 16px;
  font-weight: 600;
  margin: 0;
  color: #111;
}

.quotations-item-left p {
  font-size: 14px;
  color: #555;
  margin-top: 4px;
}

/* Right Section */
.quotations-item-right {
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: flex-end;
}

.quotations-action-btn {
  border: 1px solid #ddd;
  background: #fff;
  color: #111;
  font-size: 14px;
  border-radius: 6px;
  padding: 8px 14px;
  cursor: pointer;
  transition: background 0.2s;
}

.quotations-action-btn:hover {
  background: #f5f5f5;
}

.quotations-action-btn.download {
  background: #0073e6;
  color: #fff;
  border: none;
}

.quotations-action-btn.download:hover {
  background: #005bb5;
}

/* Pending Label */
.quotations-pending-label {
  background: #fef9c3;
  color: #92400e;
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 13px;
}

/* ---------------------------
   Empty State
---------------------------- */
.quotations-empty {
  text-align: center;
  color: #777;
  margin-top: 100px;
  font-size: 16px;
}

/* ---------------------------
   Responsive
---------------------------- */
@media (max-width: 768px) {
  .quotations-header,
  .quotations-filters,
  .quotations-list-container {
    padding: 16px 20px;
  }

  .quotations-item-body {
    flex-direction: column;
    align-items: flex-start;
  }

  .quotations-item-right {
    align-items: flex-start;
  }
}

