.register-page-container {
  display: flex;
  justify-content: center;
  padding-top: 60px;
}

.register-form-container {
  width: 420px;
  background: #fff;
  padding: 25px 30px;
  border: 1px solid #ddd;
  border-radius: 8px;
}

h2 {
  text-align: center;
  margin-bottom: 20px;
  font-weight: 600;
}

.form-group {
  margin-bottom: 15px;
}

.form-input {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  font-size: 15px;
  border-radius: 5px;
}

.password-wrapper {
  display: flex;
  align-items: center;
  position: relative;
}

.toggle-password {
  position: absolute;
  right: 12px;
  cursor: pointer;
  font-size: 18px;
}

.error {
  color: red;
  margin-top: -10px;
  margin-bottom: 10px;
  font-size: 13px;
}

.register-submit-btn {
  width: 100%;
  padding: 12px;
  background: #0074d9;
  color: white;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
}

.otp-section {
  text-align: center;
}

.otp-input {
  width: 180px;
  padding: 10px;
  text-align: center;
  font-size: 20px;
  letter-spacing: 8px;
  margin-bottom: 15px;
}

.otp-submit-btn {
  padding: 12px 40px;
  background: #0074d9;
  color: #fff;
  border-radius: 6px;
  cursor: pointer;
}
