/* -----------------------------------
   Container & Header
------------------------------------ */
.productslist-container {
  width: 100%;
  position: relative;
  background: #fff;
  margin-bottom: 32px;
  padding: 10px 0;
}

.productslist-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 24px 8px;
}

.productslist-title {
  font-size: 22px;
  font-weight: 700;
  color: #111;
}

.productslist-seeall {
  font-size: 14px;
  font-weight: 600;
  color: #0654ba;
  cursor: pointer;
  text-decoration: none;
}

.productslist-seeall:hover {
  text-decoration: underline;
}

/* -----------------------------------
   Scrollable Horizontal Strip
------------------------------------ */
.productslist-scroll {
  display: flex;
  gap: 18px;
  overflow-x: auto;
  overflow-y: hidden;
  scroll-behavior: smooth;
  padding: 8px 40px;
  scrollbar-width: none;
}

.productslist-scroll::-webkit-scrollbar {
  display: none;
}

/* -----------------------------------
   Product Card
------------------------------------ */
.product-card {
  flex: 0 0 230px;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.08);
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  display: flex;
  flex-direction: column;
}

.product-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.12);
}

/* -----------------------------------
   Image Section
------------------------------------ */
.product-image-wrapper {
  position: relative;
  width: 100%;
  height: 210px;
  overflow: hidden;
  border-radius: 12px 12px 0 0;
  background-color: #f9f9f9;
}

.product-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.product-card:hover .product-image {
  transform: scale(1.05);
}

/* -----------------------------------
   Wishlist Button
------------------------------------ */
.wishlist-btn {
  position: absolute;
  top: 8px;
  right: 8px;
  background: rgba(255, 255, 255, 0.9);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  width: 34px;
  height: 34px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.2s ease;
  z-index: 2;
}

.wishlist-btn:hover {
  background: #fff;
}

/* -----------------------------------
   Product Info
------------------------------------ */
.product-info {
  padding: 12px 14px 18px 14px;
}

.product-name {
  font-size: 14px;
  font-weight: 500;
  color: #333;
  margin-bottom: 4px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* -----------------------------------
   PRICE + DISCOUNT
------------------------------------ */
.product-price-section {
  display: flex;
  align-items: center;
  gap: 6px;
  margin-top: 4px;
}

.product-original-price {
  font-size: 13px;
  color: #777;
  text-decoration: line-through;
}

.product-discounted-price {
  font-size: 16px;
  font-weight: 700;
  color: #111;
}

.product-discount-tag {
  font-size: 12px;
  font-weight: 600;
  color: #d32f2f;
  background: #ffebee;
  padding: 2px 6px;
  border-radius: 4px;
}

/* Price unavailable */
.product-price.unavailable {
  font-size: 13px;
  color: #888;
}

/* -----------------------------------
   Empty State
------------------------------------ */
.productslist-empty {
  padding: 16px 24px;
  color: #666;
  font-size: 14px;
  text-align: center;
}

/* -----------------------------------
   Navigation Arrows
------------------------------------ */
.productslist-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 5;
}

.productslist-arrow:hover {
  background: #f2f2f2;
}

.productslist-arrow.left {
  left: 8px;
}

.productslist-arrow.right {
  right: 8px;
}

/* -----------------------------------
   Responsive
------------------------------------ */
@media (max-width: 1200px) {
  .product-card {
    flex: 0 0 210px;
  }
  .product-image-wrapper {
    height: 190px;
  }
}

@media (max-width: 992px) {
  .product-card {
    flex: 0 0 190px;
  }
  .product-image-wrapper {
    height: 170px;
  }
}

@media (max-width: 768px) {
  .product-card {
    flex: 0 0 160px;
  }
  .product-image-wrapper {
    height: 150px;
  }
  .productslist-arrow {
    display: none;
  }
}

@media (max-width: 480px) {
  .product-card {
    flex: 0 0 140px;
  }
  .product-image-wrapper {
    height: 130px;
  }
  .productslist-title {
    font-size: 18px;
  }
}
