/* Wishlist Overlay */
.wishlist-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(8px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: wishlist-fadeIn 0.3s ease;
  padding: 2rem;
}

@keyframes wishlist-fadeIn {
  from { 
    opacity: 0;
    backdrop-filter: blur(0px);
  }
  to { 
    opacity: 1;
    backdrop-filter: blur(8px);
  }
}

/* Wishlist Panel */
.wishlist-panel {
  width: 100%;
  max-width: 1200px;
  max-height: 90vh;
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-radius: 20px;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  animation: wishlist-slideUp 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

@keyframes wishlist-slideUp {
  from {
    opacity: 0;
    transform: translateY(50px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* Header */
.wishlist-header {
  background: linear-gradient(135deg, #ec4899 0%, #db2777 100%);
  padding: 1.5rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.wishlist-header::before {
  content: '';
  position: absolute;
  top: -50%;
  right: -50%;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(255,255,255,0.1) 0%, transparent 70%);
}

.wishlist-header-content h2 {
  font-size: 1.5rem;
  font-weight: 700;
  color: white;
  margin: 0 0 0.25rem 0;
  font-family: 'Inter', sans-serif;
}

.wishlist-header-content p {
  color: rgba(255, 255, 255, 0.9);
  margin: 0;
  font-size: 0.9rem;
}

.wishlist-close-btn {
  background: rgba(255, 255, 255, 0.2);
  border: none;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wishlist-close-btn:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: rotate(90deg);
}

/* Summary */
.wishlist-summary {
  display: flex;
  gap: 2rem;
  padding: 1.5rem 2rem;
  background: white;
  border-bottom: 1px solid #e2e8f0;
  justify-content: center;
}

.wishlist-summary-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 1.5rem;
  position: relative;
}

.wishlist-summary-item:not(:last-child)::after {
  content: '';
  position: absolute;
  right: -1rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1px;
  height: 30px;
  background: #e2e8f0;
}

.wishlist-summary-label {
  font-size: 0.8rem;
  color: #64748b;
  margin-bottom: 0.5rem;
  font-weight: 500;
}

.wishlist-summary-value {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1e293b;
}

/* Empty State */
.wishlist-empty {
  text-align: center;
  padding: 4rem 2rem;
  background: white;
}

.wishlist-empty-icon {
  font-size: 4rem;
  margin-bottom: 1.5rem;
  opacity: 0.5;
}

.wishlist-empty h3 {
  font-size: 1.5rem;
  color: #374151;
  margin: 0 0 1rem 0;
}

.wishlist-empty p {
  color: #6b7280;
  margin: 0;
  font-size: 1rem;
}

/* Content */
.wishlist-content {
  flex: 1;
  overflow-y: auto;
  padding: 2rem;
  background: #f8fafc;
}

.wishlist-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 1.5rem;
}

/* Wishlist Card */
.wishlist-card {
  background: white;
  border-radius: 16px;
  padding: 1.5rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  border: 1px solid #f1f5f9;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.wishlist-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: linear-gradient(135deg, #ec4899 0%, #db2777 100%);
}

.wishlist-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.12);
}

/* Card Header */
.wishlist-card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
  z-index: 2;
}

.wishlist-remove-btn {
  background: #fecdd3;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  color: #db2777;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wishlist-remove-btn:hover {
  background: #fda4af;
  transform: scale(1.1);
}

.wishlist-view-btn {
  background: #e0e7ff;
  border: none;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  color: #4f46e5;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wishlist-view-btn:hover {
  background: #c7d2fe;
  transform: scale(1.1);
}

/* Image Container */
.wishlist-image-container {
  position: relative;
  border-radius: 12px;
  overflow: hidden;
  margin-bottom: 1rem;
  cursor: pointer;
  background: #f8fafc;
}

.wishlist-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  transition: all 0.3s ease;
}

.wishlist-image-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 600;
  opacity: 0;
  transition: all 0.3s ease;
}

.wishlist-image-container:hover .wishlist-image-overlay {
  opacity: 1;
}

.wishlist-image-container:hover .wishlist-image {
  transform: scale(1.05);
}

/* Card Body */
.wishlist-card-body {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.wishlist-product-name {
  font-size: 1.1rem;
  font-weight: 700;
  color: #1e293b;
  margin: 0;
  line-height: 1.3;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.wishlist-product-category {
  font-size: 0.8rem;
  color: #64748b;
  margin: 0;
  background: #f1f5f9;
  padding: 0.25rem 0.5rem;
  border-radius: 12px;
  width: fit-content;
}

.wishlist-product-price {
  font-size: 1.25rem;
  font-weight: 700;
  color: #059669;
  margin: 0;
}

/* Card Footer */
.wishlist-card-footer {
  margin-top: auto;
}

.wishlist-add-to-cart-btn {
  width: 100%;
  padding: 0.875rem 1rem;
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  border: none;
  border-radius: 12px;
  color: white;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
}

.wishlist-add-to-cart-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(59, 130, 246, 0.4);
}

/* Footer */
.wishlist-footer {
  padding: 1.5rem 2rem;
  background: white;
  border-top: 1px solid #e2e8f0;
  text-align: center;
}

.wishlist-close-main-btn {
  padding: 1rem 2rem;
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
  border: none;
  border-radius: 12px;
  color: white;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px rgba(239, 68, 68, 0.3);
}

.wishlist-close-main-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(239, 68, 68, 0.4);
}

/* Scrollbar */
.wishlist-content::-webkit-scrollbar {
  width: 8px;
}

.wishlist-content::-webkit-scrollbar-track {
  background: #f1f5f9;
  border-radius: 4px;
}

.wishlist-content::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #ec4899 0%, #db2777 100%);
  border-radius: 4px;
}

.wishlist-content::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #d41471 0%, #be185d 100%);
}

/* Responsive */
@media (max-width: 1024px) {
  .wishlist-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }
}

@media (max-width: 768px) {
  .wishlist-overlay {
    padding: 1rem;
  }
  
  .wishlist-panel {
    max-height: 95vh;
    border-radius: 16px;
  }
  
  .wishlist-header {
    padding: 1.25rem 1.5rem;
  }
  
  .wishlist-summary {
    padding: 1rem 1.5rem;
    gap: 1rem;
  }
  
  .wishlist-summary-item {
    padding: 0 1rem;
  }
  
  .wishlist-summary-item:not(:last-child)::after {
    right: -0.5rem;
  }
  
  .wishlist-content {
    padding: 1.5rem;
  }
  
  .wishlist-grid {
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
    gap: 1rem;
  }
  
  .wishlist-card {
    padding: 1.25rem;
  }
}

@media (max-width: 640px) {
  .wishlist-grid {
    grid-template-columns: 1fr;
  }
  
  .wishlist-overlay {
    padding: 0.5rem;
  }
  
  .wishlist-header {
    padding: 1rem 1.25rem;
  }
  
  .wishlist-content {
    padding: 1rem;
  }
  
  .wishlist-card {
    padding: 1rem;
  }
  
  .wishlist-image {
    height: 180px;
  }
}