/* -----------------------------------
   Overall Page Layout
------------------------------------ */
.buyer-home-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  background: #fafafa;
}

.buyer-home-content {
  flex: 1;
  width: 100%;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.buyer-scroll-content {
  width: 100%;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* -----------------------------------
   Shared Inner Wrapper (like eBay)
------------------------------------ */
.buyer-content-wrapper {
  width: 90%; /*  same width for banner + lists */
  max-width: 1440px;
  margin: 0 auto 40px auto;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

/* -----------------------------------
   Banner Section
------------------------------------ */
.buyer-banner-wrapper {
  width: 100%;
  height: 420px;
  border-radius: 12px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #f5f5f5;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
}

.buyer-banner-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 12px;
  transition: transform 0.5s ease;
}

.buyer-banner-wrapper:hover img {
  transform: scale(1.03);
}

/* -----------------------------------
   Center Loader
------------------------------------ */
.center {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

/* -----------------------------------
   Responsive Adjustments
------------------------------------ */
@media (max-width: 1200px) {
  .buyer-content-wrapper {
    width: 92%;
  }
  .buyer-banner-wrapper {
    height: 360px;
  }
}

@media (max-width: 992px) {
  .buyer-content-wrapper {
    width: 94%;
  }
  .buyer-banner-wrapper {
    height: 300px;
  }
}

@media (max-width: 768px) {
  .buyer-content-wrapper {
    width: 96%;
  }
  .buyer-banner-wrapper {
    height: 240px;
  }
}

@media (max-width: 480px) {
  .buyer-content-wrapper {
    width: 98%;
  }
  .buyer-banner-wrapper {
    height: 200px;
  }
}
