.uploadproduct-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.uploadproduct-card {
  background: #fff;
  padding: 30px 40px;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  width: 400px;
  text-align: center;
}

.uploadproduct-card h2 {
  margin-bottom: 20px;
  color: #333;
}

.uploadproduct-card input[type="file"] {
  display: block;
  margin: 10px auto 20px;
}

.uploadproduct-btn {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 6px;
  cursor: pointer;
  margin-right: 10px;
}

.uploadproduct-btn:hover {
  background-color: #45a049;
}

.uploadproduct-cancel {
  background-color: #ccc;
  border: none;
  padding: 10px 20px;
  border-radius: 6px;
  cursor: pointer;
}

.uploadproduct-message {
  margin-top: 15px;
  font-weight: 500;
}

.uploadproduct-info {
  margin-top: 20px;
  font-size: 0.9rem;
  color: #555;
  text-align: left;
}
