.orders-panel {
  padding: 20px;
  color: #222;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  background: #fafafa;
  border-radius: 12px;
}

.orders-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.orders-header h2 {
  font-size: 1.5rem;
  font-weight: 600;
}

.close-btn {
  background: #ff5555;
  border: none;
  color: white;
  font-size: 18px;
  border-radius: 8px;
  padding: 5px 12px;
  cursor: pointer;
  transition: 0.2s ease;
}

.close-btn:hover {
  background: #e64545;
}

.order-card {
  background: white;
  border-radius: 12px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.08);
  padding: 16px 20px;
  margin-bottom: 18px;
  transition: transform 0.15s ease;
}

.order-card:hover {
  transform: translateY(-2px);
}

.order-info {
  margin-bottom: 10px;
}

.order-info h3 {
  margin-bottom: 4px;
  font-weight: 600;
}

.order-info p {
  font-size: 14px;
  margin: 2px 0;
  color: #555;
}

.order-items {
  border-top: 1px solid #eee;
  padding-top: 10px;
  margin-top: 10px;
}

.order-item {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  padding: 4px 0;
}

.order-actions {
  margin-top: 12px;
  text-align: right;
}

.order-actions select {
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 6px 10px;
  background: #f7f7f7;
  cursor: pointer;
  font-size: 14px;
}

.order-actions select:hover {
  background: #eee;
}

/* Center loader */
.center {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
}

.loader {
  border: 4px solid #eee;
  border-top: 4px solid #3498db;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
