/* QuotationPanel.css */
.quotationpanel-panel {
  background: white;
  border-radius: 16px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
  padding: 24px;
  max-width: 800px;
  width: 95%;
  max-height: 90vh;
  overflow-y: auto;
  margin: 20px auto;
  border: 1px solid #e1e8ed;
  position: relative;
}

.quotationpanel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  padding-bottom: 16px;
  border-bottom: 2px solid #f7fafc;
}

.quotationpanel-title {
  font-size: 24px;
  font-weight: 700;
  color: #2d3748;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin: 0;
}

.quotationpanel-close-btn {
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(245, 87, 108, 0.3);
}

.quotationpanel-close-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(245, 87, 108, 0.4);
}

.quotationpanel-empty-state {
  text-align: center;
  padding: 40px 20px;
  color: #718096;
  font-size: 16px;
}

.quotationpanel-empty-state p {
  margin-bottom: 20px;
}

.quotationpanel-requests-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-bottom: 24px;
}

.quotationpanel-card {
  background: #f8fafc;
  border: 2px solid #e2e8f0;
  border-radius: 12px;
  padding: 20px;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.quotationpanel-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  transition: all 0.3s ease;
}

.quotationpanel-card:hover {
  transform: translateY(-2px);
  border-color: #667eea;
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.1);
}

.quotationpanel-card:hover::before {
  width: 6px;
}

.quotationpanel-card.resolved {
  background: linear-gradient(135deg, #f0fff4 0%, #e6fffa 100%);
  border-color: #9ae6b4;
}

.quotationpanel-card.resolved::before {
  background: linear-gradient(135deg, #48bb78 0%, #38b2ac 100%);
}

.quotationpanel-card-info {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 12px;
  margin-bottom: 16px;
}

.quotationpanel-info-item {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.quotationpanel-info-label {
  font-size: 12px;
  font-weight: 600;
  color: #718096;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.quotationpanel-info-value {
  font-size: 14px;
  font-weight: 600;
  color: #2d3748;
  word-break: break-word;
}

.quotationpanel-product-name {
  font-size: 16px;
  font-weight: 700;
  color: #2d3748;
  margin-bottom: 8px;
}

.quotationpanel-status-badge {
  position: absolute;
  top: 16px;
  right: 16px;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.quotationpanel-status-badge.pending {
  background: #fed7d7;
  color: #c53030;
}

.quotationpanel-status-badge.resolved {
  background: #c6f6d5;
  color: #276749;
}

.quotationpanel-file-upload {
  position: relative;
  margin-top: 12px;
}

.quotationpanel-file-input {
  width: 100%;
  padding: 12px 16px;
  border: 2px dashed #cbd5e0;
  border-radius: 8px;
  background: white;
  font-size: 14px;
  color: #4a5568;
  cursor: pointer;
  transition: all 0.3s ease;
}

.quotationpanel-file-input:hover {
  border-color: #667eea;
  background: #f0f4ff;
}

.quotationpanel-file-input:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.quotationpanel-file-input::file-selector-button {
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-right: 12px;
}

.quotationpanel-file-input::file-selector-button:hover {
  transform: translateY(-1px);
  box-shadow: 0 2px 8px rgba(79, 172, 254, 0.3);
}

.quotationpanel-loading {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px;
  font-size: 16px;
  color: #667eea;
  font-weight: 600;
}

.quotationpanel-loading::after {
  content: "";
  width: 20px;
  height: 20px;
  border: 2px solid #667eea;
  border-top: 2px solid transparent;
  border-radius: 50%;
  margin-left: 12px;
  animation: quotationpanel-spin 1s linear infinite;
}

@keyframes quotationpanel-spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.quotationpanel-footer {
  display: flex;
  justify-content: center;
  margin-top: 24px;
  padding-top: 20px;
  border-top: 2px solid #f7fafc;
}

/* Responsive Design */
@media (max-width: 768px) {
  .quotationpanel-panel {
    padding: 20px;
    margin: 16px auto;
    width: 90%;
  }

  .quotationpanel-header {
    flex-direction: column;
    gap: 12px;
    text-align: center;
  }

  .quotationpanel-title {
    font-size: 20px;
  }

  .quotationpanel-card-info {
    grid-template-columns: 1fr;
    gap: 8px;
  }

  .quotationpanel-status-badge {
    position: static;
    align-self: flex-start;
    margin-bottom: 12px;
  }

  .quotationpanel-close-btn {
    width: 100%;
    padding: 12px;
  }
}

@media (max-width: 480px) {
  .quotationpanel-panel {
    padding: 16px;
    margin: 12px auto;
    border-radius: 12px;
  }

  .quotationpanel-title {
    font-size: 18px;
  }

  .quotationpanel-card {
    padding: 16px;
  }

  .quotationpanel-info-value {
    font-size: 13px;
  }

  .quotationpanel-file-input {
    padding: 10px 12px;
    font-size: 13px;
  }

  .quotationpanel-file-input::file-selector-button {
    padding: 6px 12px;
    font-size: 11px;
  }
}

/* Focus states for accessibility */
.quotationpanel-close-btn:focus-visible,
.quotationpanel-file-input:focus-visible {
  outline: 2px solid #667eea;
  outline-offset: 2px;
}

/* Upload progress indicator */
.quotationpanel-upload-progress {
  width: 100%;
  height: 4px;
  background: #e2e8f0;
  border-radius: 2px;
  margin-top: 8px;
  overflow: hidden;
}

.quotationpanel-upload-progress-bar {
  height: 100%;
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
  border-radius: 2px;
  transition: width 0.3s ease;
}

/* Success animation */
@keyframes quotationpanel-success {
  0% { transform: scale(1); }
  50% { transform: scale(1.05); }
  100% { transform: scale(1); }
}

.quotationpanel-card.success {
  animation: quotationpanel-success 0.6s ease;
}

/* Empty state illustration */
.quotationpanel-empty-illustration {
  font-size: 48px;
  margin-bottom: 16px;
  opacity: 0.5;
}

/* Scrollbar styling */
.quotationpanel-panel::-webkit-scrollbar {
  width: 6px;
}

.quotationpanel-panel::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

.quotationpanel-panel::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 3px;
}

.quotationpanel-panel::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #5a6fd8 0%, #6a4190 100%);
}
