.contact {
  background: white;
}

.contact-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
}

.contact-info h3 {
  font-size: 1.5rem;
  margin-bottom: 2rem;
  color: var(--text-dark);
}

.contact-methods {
  margin-bottom: 2rem;
}

.contact-method {
  margin-bottom: 1.5rem;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid var(--border);
}

.contact-method strong {
  display: block;
  margin-bottom: 0.5rem;
  color: var(--text-dark);
}

.contact-method span {
  color: var(--text-light);
  line-height: 1.5;
}

.emergency-contact {
  background: var(--primary-light);
  padding: 1.5rem;
  border-radius: 8px;
  border: 1px solid var(--border);
}

.emergency-contact h4 {
  margin-bottom: 0.5rem;
  color: var(--text-dark);
}

.emergency-contact p {
  color: var(--text-light);
  margin-bottom: 1rem;
  font-size: 0.9rem;
}

.contact-form {
  background: var(--primary-light);
  padding: 2rem;
  border-radius: 12px;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: var(--text-dark);
}

.form-group input,
.form-group select,
.form-group textarea {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid var(--border);
  border-radius: 6px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--primary-blue);
}

@media (max-width: 768px) {
  .contact-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
}