/* =============== PAGE CONTAINER =============== */
.pd-container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 1.5rem;
  background-color: #fff;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: #111;
}

/* =============== TOP BAR =============== */
.pd-topbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.pd-back-btn {
  border: none;
  background: #f3f3f3;
  color: #333;
  padding: 8px 16px;
  border-radius: 6px;
  font-size: 14px;
  cursor: pointer;
  transition: background 0.2s ease;
}

.pd-back-btn:hover {
  background: #e3e3e3;
}

/* =============== MAIN GRID =============== */
.pd-main {
  display: grid;
  grid-template-columns: 1.2fr 1fr;
  gap: 2rem;
  align-items: flex-start;
}

/* =============== GALLERY SECTION (UNCHANGED) =============== */
.pd-gallery {
  display: flex;
  gap: 1rem;
}

.pd-thumbnails {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  overflow-y: auto;
  max-height: 500px;
  padding-right: 4px;
}

.pd-thumbnails img {
  width: 70px;
  height: 70px;
  object-fit: cover;
  border-radius: 6px;
  cursor: pointer;
  border: 2px solid transparent;
  transition: all 0.2s ease;
}

.pd-thumbnails img:hover {
  border-color: #ccc;
}

.pd-thumbnails img.active {
  border-color: #0073e6;
}

.pd-main-image {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fafafa;
  border-radius: 8px;
  position: relative;
}

.pd-main-image img {
  width: 100%;
  max-height: 500px;
  object-fit: contain;
  border-radius: 8px;
}

/* ===========================================================
   RIGHT SIDE PANEL (PRODUCT INFO) 
   =========================================================== */

.pd-info {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.pd-title {
  font-size: 1.8rem;
  font-weight: 700;
  line-height: 1.3;
  margin-bottom: 0.3rem;
}

.pd-seller {
  font-size: 0.95rem;
  color: #555;
}

.pd-seller span {
  color: #0064d2;
  cursor: pointer;
}

.pd-seller span:hover {
  text-decoration: underline;
}

/* PRICE */
.pd-price-section {
  margin-top: 0.5rem;
}

.pd-price {
  font-size: 1.9rem;
  font-weight: 700;
  color: #111;
}

.pd-price-unavailable {
  font-size: 1rem;
  color: #999;
}

/* ===========================================================
   PRODUCT ATTRIBUTES BOX (Ref No / Brand)
   =========================================================== */

.pd-attributes-box {
  background: #fafafa;
  border: 1px solid #e5e5e5;
  padding: 14px 16px;
  border-radius: 8px;
  margin-top: 1rem;
}

.pd-attr-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 6px;
}

.pd-attr-row:last-child {
  margin-bottom: 0;
}

.pd-attr-label {
  font-weight: 600;
  color: #444;
}

.pd-attr-value {
  font-weight: 500;
  color: #111;
}

/* ===========================================================
   VARIANT SELECTOR
   =========================================================== */

.pd-variants {
  margin-top: 0.5rem;
}

.pd-variant-buttons {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
  margin-top: 0.5rem;
}

.pd-variant-buttons button {
  border: 1px solid #ccc;
  background: white;
  border-radius: 5px;
  padding: 6px 12px;
  cursor: pointer;
  transition: all 0.2s;
}

.pd-variant-buttons button.active {
  border-color: #0073e6;
  background: #e7f1ff;
  color: #005bb5;
}

/* ===========================================================
   QUANTITY SELECTOR
   =========================================================== */

.pd-quantity {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.pd-quantity label {
  font-weight: 600;
}

.pd-quantity-controls {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.pd-quantity-controls button {
  width: 32px;
  height: 32px;
  font-size: 18px;
  border: 1px solid #ccc;
  background: #f9f9f9;
  border-radius: 4px;
  cursor: pointer;
}

.pd-quantity-controls button:hover {
  background: #e7e7e7;
}

.pd-quantity-controls input {
  width: 60px;
  text-align: center;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-weight: 600;
}

/* ===========================================================
   ACTION BUTTONS
   =========================================================== */

.pd-action-buttons {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.pd-add-btn {
  padding: 12px;
  background: #0073e6;
  color: white;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  border: none;
  cursor: pointer;
}

.pd-add-btn:hover {
  background: #005bb5;
}

/* Quotation Button */
.pd-quote-btn {
  padding: 12px;
  background: #ff8c00;
  color: #111;
  border-radius: 8px;
  border: none;
  font-weight: 700;
  font-size: 16px;
  cursor: pointer;
}

.pd-quote-btn:hover {
  background: #e67a00;
}

/* ===========================================================
   DETAILS
   =========================================================== */

.pd-details {
  margin-top: 3rem;
  border-top: 1px solid #eee;
  padding-top: 2rem;
}

/* ===========================================================
   REVIEWS
   =========================================================== */

.pd-reviews {
  margin-top: 3rem;
  border-top: 1px solid #eee;
  padding-top: 2rem;
}

.pd-review-card {
  background: #f9f9f9;
  border: 1px solid #eee;
  border-left: 4px solid #0073e6;
  padding: 1rem;
}

/* ===========================================================
   RESPONSIVE
   =========================================================== */

@media (max-width: 1000px) {
  .pd-main {
    grid-template-columns: 1fr;
  }

  .pd-gallery {
    flex-direction: column;
  }

  .pd-thumbnails {
    flex-direction: row;
    justify-content: center;
  }

  .pd-main-image img {
    max-height: 350px;
  }
}

