/* Login.css */
.login-form-container {
  max-width: 440px;
  margin: 60px auto;
  padding: 48px 40px;
  background: #ffffff;
  border-radius: 24px;
  box-shadow: 
    0 4px 6px -1px rgba(0, 0, 0, 0.05),
    0 10px 15px -3px rgba(0, 0, 0, 0.04),
    0 20px 25px -5px rgba(0, 0, 0, 0.02);
  border: 1px solid #f8fafc;
  position: relative;
  overflow: hidden;
}

.login-form-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #334155, #64748b, #94a3b8);
  background-size: 200% 100%;
  animation: login-shimmer 3s ease-in-out infinite;
}

@keyframes login-shimmer {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

.login-form-container h2 {
  text-align: center;
  font-size: 32px;
  font-weight: 800;
  color: #0f172a;
  margin-bottom: 8px;
  background: linear-gradient(135deg, #334155 0%, #64748b 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  letter-spacing: -0.5px;
}

.login-form-subtitle {
  text-align: center;
  color: #64748b;
  font-size: 14px;
  margin-bottom: 32px;
  font-weight: 400;
}

.login-form-container form {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.login-form-group {
  display: flex;
  flex-direction: column;
  gap: 6px;
  position: relative;
}

/* Enhanced FormInput styles for Login */
.login-form-container :global(.forminput-container) {
  margin-bottom: 0;
}

.login-form-container :global(.forminput-input) {
  width: 100%;
  padding: 16px 18px;
  border: 1.5px solid #e2e8f0;
  border-radius: 12px;
  font-size: 15px;
  transition: all 0.2s ease;
  background-color: #ffffff;
  color: #0f172a;
  font-family: inherit;
  box-sizing: border-box;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.03);
}

.login-form-container :global(.forminput-input):focus {
  outline: none;
  border-color: #475569;
  background-color: #ffffff;
  box-shadow: 
    0 0 0 3px rgba(71, 85, 105, 0.08),
    0 1px 2px 0 rgba(0, 0, 0, 0.03);
  transform: translateY(-1px);
}

.login-form-container :global(.forminput-input):hover {
  border-color: #cbd5e1;
  background-color: #fafafa;
}

.login-form-container :global(.forminput-input)::placeholder {
  color: #94a3b8;
  font-weight: 400;
}

.login-submit-btn {
  background: #334155;
  color: white;
  border: none;
  padding: 16px 24px;
  border-radius: 12px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 8px;
  box-shadow: 
    0 4px 6px -1px rgba(51, 65, 85, 0.15),
    0 2px 4px -1px rgba(51, 65, 85, 0.08);
  position: relative;
  overflow: hidden;
  letter-spacing: 0.5px;
  width: 100%;
}

.login-submit-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  background: #475569;
  box-shadow: 
    0 10px 15px -3px rgba(51, 65, 85, 0.2),
    0 4px 6px -2px rgba(51, 65, 85, 0.05);
}

.login-submit-btn:active:not(:disabled) {
  transform: translateY(0);
  background: #1e293b;
}

.login-submit-btn:disabled {
  background: #cbd5e1;
  color: #64748b;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
  opacity: 0.7;
}

.login-submit-btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.15), transparent);
  transition: left 0.5s;
}

.login-submit-btn:hover::before {
  left: 100%;
}

.login-register-link {
  text-align: center;
  margin-top: 32px;
  padding-top: 24px;
  border-top: 1px solid #f1f5f9;
  color: #64748b;
  font-size: 14px;
}

.login-link {
  color: #475569;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  text-decoration: none;
  margin-left: 4px;
}

.login-link:hover {
  color: #334155;
  text-decoration: underline;
}

/* Enhanced Error States */
.login-error-message {
  color: #dc2626;
  font-size: 13px;
  margin-top: 4px;
  padding: 8px 12px;
  background: #fef2f2;
  border: 1px solid #fecaca;
  border-radius: 8px;
  display: flex;
  align-items: center;
  gap: 6px;
  font-weight: 500;
}

.login-error-message::before {
  content: "";
  font-size: 14px;
}

/* Loading Animation */
@keyframes login-pulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}

.login-submit-btn:disabled {
  animation: login-pulse 1.5s ease-in-out infinite;
}

.login-loading-spinner {
  display: inline-block;
  width: 16px;
  height: 16px;
  border: 2px solid transparent;
  border-top: 2px solid white;
  border-radius: 50%;
  animation: login-spin 1s linear infinite;
  margin-right: 8px;
}

@keyframes login-spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Success State */
@keyframes login-success {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.02);
  }
  100% {
    transform: scale(1);
  }
}

.login-form-container.success {
  animation: login-success 0.6s ease;
}

/* Additional Login Features */
.login-remember-forgot {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: -8px;
  margin-bottom: 8px;
}

.login-remember {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  color: #64748b;
  cursor: pointer;
}

.login-remember input[type="checkbox"] {
  width: 16px;
  height: 16px;
  border-radius: 4px;
  border: 1.5px solid #cbd5e1;
  cursor: pointer;
  background: #ffffff;
}

.login-remember input[type="checkbox"]:checked {
  background-color: #334155;
  border-color: #334155;
}

.login-forgot-password {
  color: #475569;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.login-forgot-password:hover {
  color: #334155;
  text-decoration: underline;
}

/* Social Login Section */
.login-social-divider {
  display: flex;
  align-items: center;
  text-align: center;
  margin: 24px 0;
  color: #64748b;
  font-size: 14px;
}

.login-social-divider::before,
.login-social-divider::after {
  content: "";
  flex: 1;
  border-bottom: 1px solid #e2e8f0;
}

.login-social-divider span {
  padding: 0 16px;
}

.login-social-buttons {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
  margin-bottom: 24px;
}

.login-social-btn {
  padding: 12px;
  border: 1.5px solid #e2e8f0;
  border-radius: 12px;
  background: white;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  font-size: 14px;
  font-weight: 500;
  color: #334155;
}

.login-social-btn:hover {
  border-color: #475569;
  background: #f8fafc;
  transform: translateY(-1px);
}

.login-social-btn.google::before {
  content: "G";
  font-weight: bold;
  color: #334155;
}

.login-social-btn.facebook::before {
  content: "f";
  font-weight: bold;
  color: #334155;
}

/* Password Visibility Toggle */
.login-password-toggle {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  color: #64748b;
  cursor: pointer;
  padding: 4px;
  border-radius: 4px;
  transition: all 0.2s ease;
}

.login-password-toggle:hover {
  color: #334155;
  background: #f1f5f9;
}

/* Responsive Design */
@media (max-width: 768px) {
  .login-form-container {
    margin: 40px auto;
    padding: 32px 24px;
    width: 90%;
    border-radius: 20px;
  }

  .login-form-container h2 {
    font-size: 28px;
  }

  .login-form-container form {
    gap: 20px;
  }

  .login-form-container :global(.forminput-input) {
    padding: 14px 16px;
    font-size: 16px;
  }

  .login-submit-btn {
    padding: 14px 20px;
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  .login-form-container {
    padding: 24px 20px;
    margin: 20px auto;
    border-radius: 16px;
  }

  .login-form-container h2 {
    font-size: 24px;
  }

  .login-form-subtitle {
    font-size: 13px;
    margin-bottom: 24px;
  }

  .login-form-container :global(.forminput-input) {
    padding: 12px 14px;
    font-size: 16px;
  }

  .login-submit-btn {
    padding: 14px 16px;
    font-size: 16px;
  }

  .login-register-link {
    font-size: 13px;
    margin-top: 24px;
    padding-top: 20px;
  }

  .login-remember-forgot {
    flex-direction: column;
    gap: 12px;
    align-items: flex-start;
  }
}

/* Focus states for accessibility */
.login-form-container :global(.forminput-input):focus-visible,
.login-submit-btn:focus-visible,
.login-link:focus-visible,
.login-forgot-password:focus-visible {
  outline: 2px solid #475569;
  outline-offset: 2px;
}

/* Welcome back animation */
@keyframes login-welcome {
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.login-form-container h2 {
  animation: login-welcome 0.6s ease-out;
}

/* Input focus labels (if you add labels later) */
.login-form-group:focus-within .login-label {
  color: #475569;
  transform: translateY(-2px);
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .login-form-container {
    border: 2px solid #000;
  }
  
  .login-form-container :global(.forminput-input) {
    border: 2px solid #000;
  }
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  .login-form-container :global(.forminput-input),
  .login-submit-btn {
    transition: none;
    animation: none;
  }
  
  .login-submit-btn::before {
    display: none;
  }
  
  .login-form-container::before {
    animation: none;
  }
}