/* ===========================
   Main Container & Header
=========================== */
.updateproduct-details-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 24px;
  background: white;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  border: 1px solid #e1e8ed;
}

/*  Header Row: Back + Delete */
.updateproduct-header-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  flex-wrap: wrap;
  gap: 12px;
}

/* Back Button */
.updateproduct-back-button {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 12px 20px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(102, 126, 234, 0.3);
}

.updateproduct-back-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.4);
}

/*  Delete Button */
.updateproduct-delete-button {
  background: linear-gradient(135deg, #e53e3e 0%, #c53030 100%);
  color: white;
  border: none;
  padding: 12px 20px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(229, 62, 62, 0.3);
}

.updateproduct-delete-button:hover {
  background: linear-gradient(135deg, #c53030 0%, #9b2c2c 100%);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(229, 62, 62, 0.4);
}

.updateproduct-delete-button:disabled {
  background: #a0aec0;
  cursor: not-allowed;
  opacity: 0.7;
  box-shadow: none;
}

/* ===========================
   Image Upload Section
=========================== */
.updateproduct-image-wrapper {
  position: relative;
  margin-bottom: 24px;
  text-align: center;
  border: 2px dashed #e2e8f0;
  border-radius: 12px;
  padding: 20px;
  transition: all 0.3s ease;
  background: #fafafa;
}

.updateproduct-image-wrapper:hover {
  border-color: #667eea;
  background: #f0f4ff;
}

.updateproduct-product-image {
  width: 100%;
  max-width: 300px;
  height: 250px;
  object-fit: contain;
  border-radius: 8px;
  margin-bottom: 12px;
  background: white;
  padding: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.updateproduct-change-image-label {
  display: inline-block;
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  color: white;
  padding: 10px 16px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(245, 87, 108, 0.3);
}

.updateproduct-change-image-label:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(245, 87, 108, 0.4);
}

.updateproduct-change-image-label input[type="file"] {
  display: none;
}

/* ===========================
   Input Fields
=========================== */
.updateproduct-product-input {
  width: 100%;
  padding: 16px;
  margin-bottom: 16px;
  border: 2px solid #e2e8f0;
  border-radius: 12px;
  font-size: 16px;
  background: #fafafa;
  color: #2d3748;
  transition: all 0.3s ease;
  font-family: inherit;
  box-sizing: border-box;
}

.updateproduct-product-input:focus {
  outline: none;
  border-color: #667eea;
  background: white;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
  transform: translateY(-2px);
}

.updateproduct-product-input:hover {
  border-color: #cbd5e0;
  background: white;
}

.updateproduct-textarea {
  min-height: 120px;
  resize: vertical;
  line-height: 1.5;
}

.updateproduct-product-input::placeholder {
  color: #a0aec0;
}

/* ===========================
   Variants Section
=========================== */
.updateproduct-variants-title {
  font-size: 20px;
  font-weight: 700;
  color: #4a5568;
  margin-top: 20px;
  margin-bottom: 8px;
}

.updateproduct-variant-row {
  display: flex;
  align-items: center;
  gap: 10px;
  background-color: #f8fafc;
  border-radius: 10px;
  border: 2px dashed #e2e8f0;
  padding: 12px;
  transition: all 0.3s ease;
}

.updateproduct-variant-row:hover {
  border-color: #667eea;
  background-color: #f0f4ff;
}

.variant-input {
  flex: 1;
  padding: 12px;
  border: 2px solid #e2e8f0;
  border-radius: 8px;
  font-size: 15px;
  background-color: #fff;
  transition: all 0.3s ease;
}

.variant-input:focus {
  border-color: #667eea;
  outline: none;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

/* Add / Remove Variant Buttons */
.updateproduct-addvariant-btn {
  align-self: flex-start;
  background: linear-gradient(135deg, #48bb78 0%, #38a169 100%);
  color: white;
  border: none;
  padding: 10px 18px;
  border-radius: 8px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 10px;
  box-shadow: 0 2px 8px rgba(72, 187, 120, 0.3);
}

.updateproduct-addvariant-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 16px rgba(72, 187, 120, 0.4);
}

.updateproduct-remove-btn {
  background-color: #fed7d7;
  color: #c53030;
  border: none;
  border-radius: 50%;
  width: 34px;
  height: 34px;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
  transition: all 0.3s ease;
}

.updateproduct-remove-btn:hover {
  background-color: #feb2b2;
  transform: scale(1.05);
}

/* ===========================
   Update Button
=========================== */
.updateproduct-update-button {
  width: 100%;
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
  color: white;
  border: none;
  padding: 16px 32px;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 16px;
  box-shadow: 0 4px 15px rgba(79, 172, 254, 0.3);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.updateproduct-update-button:hover:not(:disabled) {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(79, 172, 254, 0.4);
}

.updateproduct-update-button:disabled {
  background: #a0aec0;
  cursor: not-allowed;
  opacity: 0.7;
  animation: updateproduct-pulse 1.5s ease-in-out infinite;
}

/* ===========================
   Animation & States
=========================== */
@keyframes updateproduct-pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.7; }
}

/* Focus States */
.updateproduct-back-button:focus-visible,
.updateproduct-delete-button:focus-visible,
.updateproduct-change-image-label:focus-visible,
.updateproduct-product-input:focus-visible,
.updateproduct-update-button:focus-visible {
  outline: 2px solid #667eea;
  outline-offset: 2px;
}

/* Error States */
.updateproduct-product-input.error {
  border-color: #e53e3e;
  background-color: #fed7d7;
}

.updateproduct-error-message {
  color: #e53e3e;
  font-size: 14px;
  margin-top: -8px;
  margin-bottom: 12px;
  padding: 0 8px;
}

/* ===========================
   Responsive Design
=========================== */
@media (max-width: 768px) {
  .updateproduct-details-container {
    padding: 20px;
    margin: 16px;
    border-radius: 12px;
  }

  .updateproduct-header-row {
    flex-direction: column;
    align-items: stretch;
  }

  .updateproduct-back-button,
  .updateproduct-delete-button {
    width: 100%;
  }

  .updateproduct-variant-row {
    flex-direction: column;
    align-items: stretch;
  }

  .variant-input {
    width: 100%;
  }

  .updateproduct-addvariant-btn {
    width: 100%;
    text-align: center;
  }

  .updateproduct-product-input {
    padding: 14px;
    font-size: 15px;
    margin-bottom: 14px;
  }

  .updateproduct-update-button {
    padding: 14px 24px;
    font-size: 15px;
  }
}

@media (max-width: 480px) {
  .updateproduct-details-container {
    padding: 16px;
    margin: 12px;
    border-radius: 10px;
  }

  .updateproduct-product-image {
    max-width: 100%;
    height: 180px;
  }

  .updateproduct-product-input {
    padding: 12px;
    font-size: 14px;
    margin-bottom: 12px;
  }

  .updateproduct-update-button {
    padding: 14px 20px;
    font-size: 14px;
  }
}
