.about {
  background: white;
  padding: 10%;
}

.about-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
}

.about-text .section-title {
  text-align: left;
  margin-bottom: 1.5rem;
}

.about-text > p {
  font-size: 1.125rem;
  color: var(--text-light);
  margin-bottom: 2rem;
  line-height: 1.7;
}

.about-features {
  display: grid;
  gap: 1.5rem;
}

.about-feature h4 {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: var(--text-dark);
}

.about-feature p {
  color: var(--text-light);
  line-height: 1.6;
}

.certification-card {
  background: var(--primary-light);
  padding: 2rem;
  border-radius: 12px;
  border: 1px solid var(--border);
}

.certification-card h3 {
  margin-bottom: 1.5rem;
  color: var(--text-dark);
  font-weight: 600;
}

.certifications {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  margin-bottom: 2rem;
}

.certification {
  background: white;
  padding: 1rem;
  border-radius: 6px;
  text-align: center;
  font-weight: 600;
  color: var(--primary-blue);
  border: 1px solid var(--border);
}

.compliance-stats {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.compliance-stat {
  text-align: center;
  padding: 1rem;
  background: white;
  border-radius: 6px;
}

.compliance-stat .stat {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--success);
  margin-bottom: 0.5rem;
}

.compliance-stat .label {
  font-size: 0.875rem;
  color: var(--text-light);
}

@media (max-width: 768px) {
  .about-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .certifications {
    grid-template-columns: 1fr;
  }

  .compliance-stats {
    grid-template-columns: 1fr;
  }
}